body{
  background-color: black;
  color: white;
}
.container{
  padding-top: 100px;
   display: flex; 
   justify-content: center;
   align-items: center;
}
.grid{
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr ;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr 1fr ;
  grid-template-areas:
  "screen screen screen screen"
  "AC C divide multiply"
  "seven eight nine minus"
  "four five six add"
  "one two three equal"
  "zero zero point equal "
  ;
}
.screen{
  grid-area: screen;
  background-color: rgba(44, 44, 44, 0.5);
  text-align: right;
  font-size: 20px;
}

.zero{
  grid-area: zero;
}
.equal{
  grid-area: equal;
}
.padButton{
  cursor: pointer;
  font-size: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 40px;
  background-color: #666666;
  border:1px solid white;
}
.dark-number{
  background-color: #4d4d4d;
}
.red-button{
  background-color: #ac3939;
}
.blueButton{
  background-color: #004466;
}
.resultOperation{
  padding: 5px 20px;
  color: white;
}